<template>
  <div>
    <el-table :data="items">
      <!-- <el-table-column prop="id" label="ID"></el-table-column> -->

      <el-table-column prop="name" label="名称" sortable>
        <template #default="scope">
          <el-button type="primary" link :title="scope.row.path" @click="handleOpenItem(scope.row)">
            {{ scope.row.name }}
          </el-button>
        </template>
      </el-table-column>

      <el-table-column prop="type" label="类型" sortable></el-table-column>

      <el-table-column prop="state" label="状态">
        <template #default="scope">
          <MyFileState :code="scope.row.state" />
        </template>
      </el-table-column>

      <el-table-column label="操作" width="170px">
        <template #default="scope">
          <el-button link type="primary" @click="handleOpenItem(scope.row)">打开</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import MyFileState from "@/components/utils/filestate.vue";

export default {
  name: "ProjectTable",

  components: { MyFileState },

  data() {
    return {};
  },

  computed: {
    items() {
      return this.$store.getters["project/listCurrentPage"];
    },
  },

  methods: {
    fetch() {
      this.$store.dispatch("project/fetch");
    },

    handleOpenItem(item) {
      let intent = { project: item };
      let name = 'intent'
      let e = { name, intent }
      this.$emit('action', e)
    }
  },

  mounted() {
    this.fetch();
  },
};
</script>
